'use strict'

const test = require('ava')
const Db = require('../')
const uuid = require('uuid-base62')
const r = require('rethinkdb')
const dbName = `plazigram_${uuid.v4()}`
const db = new Db({db: dbName})

test.before('setup db', async t => {
  await db.connect()
  t.true(db.connected, 'should be connect')
})

test.after('disconnect database', async t => {
  await db.disconnect()
  t.false(db.connected, 'should not be connect')
})

test.after.always('cleanup database', async t => {
  let conn = await r.connect({})
  await r.dbDrop(dbName).run(conn)
})

test('save image', async t => {
  t.is(typeof db.saveImage, 'function', 'saveImage is function')
})
